import React, { Component } from 'react';
import './Order.css';
import { Button, Modal } from 'antd-mobile';
import Axios from 'axios';

class Order extends Component {
    constructor(props) {
        super(props);
        this.pay = this.pay.bind(this);
        this.refund = this.refund.bind(this);
        this.cancel = this.cancel.bind(this);

        this.state = {
            orderStates: [
                '',
                '',
                '',
                '',
                '',
                ''
            ],
            orderButton: [
                <div className="order-footer"><Button className="order-button" size="small" onClick={this.pay}></Button><Button className="order-button" onClick={this.cancel} size="small"></Button></div>,
                <div className="order-footer"><Button className="order-button" size="small" onClick={this.refund}></Button></div>,
                <div className="order-footer"></div>,
                <div className="order-footer"></div>,
                <div className="order-footer"></div>,
                <div className="order-footer"></div>
            ]
        }
    };
    render() {
        return (
            <div className="Order">
                <div className="order-header">
                    <span>{this.props.id}</span>
                    <span>{this.state.orderStates[this.props.state]}</span>
                </div>
                <div className="order-body">
                    <img className="order-img" src={this.props.imageUrl} alt=""/>
                    <div className="order-detail">
                        <div className="order-title">
                            {this.props.title}
                        </div>
                        <div className="order-price">
                            {this.props.price}
                        </div>
                    </div>
                </div>

                {this.state.orderButton[this.props.state]}

            </div>
        );
    };
    pay() {
        Modal.alert('', '', [
            { text: '', onPress: () => {}, style: 'default' },
            { text: '', onPress: () => {
                let param = new URLSearchParams();
                param.append('oid', this.props.id);
                param.append('status', '');
                param.append('total', this.props.price);
                Axios.post("/SSM/test/OrderHandler_updateOrder", param)
                .then(() => {
                    this.props.changeState(1, this.props.id);
                })
                .catch(err => {
                    alert("");
                })
            }}]);
    };
    refund() {
        Modal.alert('', '', [
            { text: '', onPress: () => {}, style: 'default' },
            { text: '', onPress: () => {
                let param = new URLSearchParams();
                param.append('oid', this.props.id);
                param.append('status', '');
                param.append('total', this.props.price);
                Axios.post("/SSM/test/OrderHandler_updateOrder", param)
                .then(() => {
                    this.props.changeState(4, this.props.id);
                })
                .catch(err => {
                    alert("");
                })
            }}]);
    };
    cancel() {
        Modal.alert('', '', [
            { text: '', onPress: () => {}, style: 'default' },
            { text: '', onPress: () => {
                let param = new URLSearchParams();
                param.append('oid', this.props.id);
                param.append('status', '');
                param.append('total', 0);
                Axios.post("/SSM/test/OrderHandler_updateOrder", param)
                .then(() => {
                    this.props.changeState(3, this.props.id);
                })
                .catch(err => {
                    alert("");
                })
            }}]);
    };
}

export default Order;


