import React, { Component } from 'react';
import './Buy.css';
import {InputItem, Button, Modal} from 'antd-mobile';
import Loading from '../components/Loading';
import Axios from 'axios';

class Buy extends Component {
    constructor(props) {
        super(props);
        this.changeName = this.changeName.bind(this);
        this.changePhone = this.changePhone.bind(this);
        this.buy = this.buy.bind(this);

        this.state = {
            hideLoading: false,
            lid: 1,
            price: 120,
            name: '',
            phone: '',
            courseName: 'Spring Cloud',
            imageUrl: 'image/course1.jpg'
        }
    };
    componentWillMount() {
      window.scrollTo(0, 0);
    };
    render() {
        return (
            <div className="Buy">
                <Loading hide={this.state.hideLoading}/>
                <div className="page-title">
                    
                </div>
                <div className="buy-image">
                    <img src={this.state.imageUrl} alt=""/>
                    <div className="course-name">{this.state.courseName}</div>
                </div>
                <div className="buy-body">
                    <InputItem className="input" onBlur={this.changeName} placeholder=""><i className="icon user"></i></InputItem>
                    <InputItem className="input" onBlur={this.changePhone} type="phone" placeholder=""><i className="icon phone"></i></InputItem>
                    <Button className="form-button" onClick={this.buy}>{this.state.price}</Button>
                    <Button className="form-button"><i className="icon comment"></i></Button>
                </div>
            </div>
        );
    };
    componentDidMount() {

        Axios.post("/SSM/test/CustomerHandler_islogin")
        .then(res => {
        if (res.data.result) {
            // this.setState({hideLoading: true});
        } else {
            window.location.hash = 'login';
        }
        })
        .catch(err => {
        alert(err);
        // window.location.assign("../#login");
        });

        let href = window.location.href;
        let search = href.match(/lid=[a-zA-Z\d-_]+/);
        let id = '';

        if (search) {
            id = search[0].split('=')[1];

            if (id) {
                Axios.post("/SSM/test/LessonHandler_findLessonById?lid=" + id)
                .then(res => {
                    if (res.data) {
                        this.setState({
                            "id": id,
                            hideLoading: true,
                            price: res.data.lprice,
                            courseName: res.data.lname,
                            imageUrl: res.data.imgurl
                        });
                    } else {
                        alert("");
                    }
                })
                .catch(err => {
                    alert(err);
                })
            }
        } else {
            alert("");
        }

        // setTimeout(() =>
        //     this.setState({hideLoading: true}),
        //     1000
        // );
    };
    changeName(e) {
        this.setState({
            name: e
        });
    };
    changePhone(e) {
         this.setState({
             phone: e.replace(/\s/g, '')
         })
    };
    buy() {
        Modal.alert("", "", [
            { text: '', onPress: () => {
                if (/^1\d{10}/.test(this.state.phone)) {
                    if (this.state.name) {
                        let param = new URLSearchParams();
                        param.append('lid', this.state.id);
                        param.append('total', 0);
                        param.append('nickname', this.state.name);
                        param.append('tel', this.state.phone);
                        param.append('status', '');
                        Axios.post("/SSM/test/OrderHandler_saveOrder", param)
                        .then(res => {
                            if (res.data.result) {
                                alert("");
                                window.location.hash = "orders";
                            } else {
                                alert("");
                            }
                        })
                        .catch(err => {
                            alert(err);
                        })
                    } else {
                        alert("");
                    }
                } else {
                    alert("");
                }
            }, style: 'default' },
            { text: '', onPress: () => {
                if (/^1\d{10}/.test(this.state.phone)) {
                    if (this.state.name) {
                        let param = new URLSearchParams();
                        param.append('lid', this.state.id);
                        param.append('total', this.state.price);
                        param.append('nickname', this.state.name)
                        param.append('tel', this.state.phone);
                        param.append('status', '');
                        Axios.post("/SSM/test/OrderHandler_saveOrder", param)
                        .then(res => {
                            if (res.data.result) {
                                alert("");
                                window.location.hash = "orders";
                            } else {
                                alert("");
                            }
                        })
                        .catch(err => {
                            alert(err);
                        })
                    } else {
                        alert("");
                    }
                } else {
                    alert("");
                }
                } 
            }]
        );
    }

}

export default Buy;



